import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Note } from 'src/app/core/models/note.model';

export enum NoteManageModes {
  ADD = 'add',
  EDIT = 'edit'
}

@Component({
  selector: 'app-manage-note',
  templateUrl: './manage-note.component.html',
  styleUrls: ['./manage-note.component.scss'],
})
export class ManageNoteComponent implements OnInit {
  noteForm: FormGroup;
  mode: NoteManageModes = NoteManageModes.ADD;
  manageModes = NoteManageModes;
  note: Note;
  constructor(
    private modalController: ModalController,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.noteForm = this.fb.group({
      title: ['', []],
      description: ['', []]
    });

    if (this.note) {
      this.noteForm.patchValue(this.note);
    }
  }

  submitForm() {
    let params = this.noteForm.value;
    if (this.mode === this.manageModes.EDIT) {
      params = {
        ...this.note,
        ...params
      };
    }
    this.modalController.dismiss(params);
  }

  dismiss() {
    this.modalController.dismiss();
  }

}
